td {
  border: 1px solid black;
  text-align: center;
  min-width: 50px;
  max-width: 100px;
  min-height: 50px;
  max-height: 100px;
  width: 75px;
  height: 75px;
}

#board-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
